import Vue from 'vue'
import VueRouter from 'vue-router'

import Carousel from './components/Carousel';
import MedicoDomicilio from './components/MedicoDomicilio';
import EnfermeraDomicilio from './components/EnfermeraDomicilio';
import LaboratorioDomicilio from './components/LaboratorioDomicilio';
import FarmaciaDomicilio from './components/FarmaciaDomicilio';
import EstimulacionTemprana from './components/EstimulacionTemprana';
import PsicologaDomicilio from './components/PsicologaDomicilio';

Vue.use(VueRouter);
const router = new VueRouter({
    mode: "history",
    routes: [
        {
            path: '/',
            component: Carousel
        },
        {
            path: '/medico-domicilio',
            component: MedicoDomicilio,
        },
        {
            path: '/enfermera-domicilio',
            component: EnfermeraDomicilio,
        },
        {
            path: '/laboratorio-domicilio',
            component: LaboratorioDomicilio
        },
        {
            path: '/farmacia-domicilio',
            component: FarmaciaDomicilio
        },
        {
            path: '/estimulacion-temprana',
            component: EstimulacionTemprana
        },
        {
            path: '/psicologa-domicilio',
            component: PsicologaDomicilio
        },
    ]
});

export default router